import { BreadCrumb } from 'primereact/breadcrumb';
import { Button } from 'primereact/button';
import { Column } from 'primereact/column';
import { DataSource } from 'components/DataSource';
import { DataTable } from 'primereact/datatable';
import { Dropdown } from 'primereact/dropdown';
import { FilterTags } from 'components/FilterTags';
import { Link } from 'react-router-dom';
import { PageRequestError } from 'components/PageRequestError';
import { Paginator } from 'primereact/paginator';
import { ProgressSpinner } from 'primereact/progressspinner';
import { SplitButton } from 'primereact/splitbutton';
import { Title } from 'components/Title';
import useApp from 'hooks/useApp';

import useListPage from 'hooks/useListPage';
import useFilters from 'hooks/useFilters';
const KbmGuruListPage = (props) => {
		const app = useApp();
	const filterSchema = {
		kbm_guru_nama: {
			tagTitle: "Nama Guru",
			value: '',
			valueType: 'single',
			options: [],
		}
	}
	const pageFilterController = useFilters(filterSchema);
	const pageController = useListPage(props, pageFilterController);
	const { records, pageReady, loading, selectedItems, sortBy, sortOrder, apiRequestError, setSelectedItems, getPageBreadCrumbs, onSort, deleteItem, pagination } = pageController;
	const { filters, setFilterValue, setFilterOptions } = pageFilterController;
	const { totalRecords, totalPages, recordsPosition, firstRow, currentPage, limit, onPageChange } =  pagination;
	function ActionButton(data){
		const items = [
		{
			label: "View",
			command: (event) => { app.navigate(`/kbm_guru/view/${data.id_kehadiran_ajar}`) },
			icon: "pi pi-eye"
		},
		{
			label: "Edit",
			command: (event) => { app.navigate(`/kbm_guru/edit/${data.id_kehadiran_ajar}`) },
			icon: "pi pi-pencil"
		},
		{
			label: "Delete",
			command: (event) => { deleteItem(data.id_kehadiran_ajar) },
			icon: "pi pi-trash"
		}
	]
		return (<SplitButton dropdownIcon="pi pi-bars" className="dropdown-only p-button-text p-button-plain" model={items} />);
	}
	function NamaTemplate(data){
		if(data){
			return (
				<>{data.nama && <Button className="p-button-text" icon="pi pi-eye" label={data.tbl_guru_nama_guru} to={`/tbl_guru//${data.nama}`} /> }</>
			);
		}
	}
	function BidangStudyTemplate(data){
		if(data){
			return (
				<>{data.bidang_study && <Button className="p-button-text" icon="pi pi-eye" label={data.tbl_bidang_study_nama_bidang} to={`/tbl_bidang_study//${data.bidang_study}`} /> }</>
			);
		}
	}
	function KelasTemplate(data){
		if(data){
			return (
				<>{data.kelas && <Button className="p-button-text" icon="pi pi-eye" label={data.tbl_kelas_nama_kelas} to={`/tbl_kelas//${data.kelas}`} /> }</>
			);
		}
	}
	function JamKeTemplate(data){
		if(data){
			return (
				<>{data.jam_ke && <Button className="p-button-text" icon="pi pi-eye" label={data.tbl_jam_nama_jam} to={`/tbl_jam//${data.jam_ke}`} /> }</>
			);
		}
	}
	function PageLoading(){
		if(loading){
			return (
				<>
					<div className="flex align-items-center justify-content-center text-gray-500 p-3">
						<div><ProgressSpinner style={{width:'30px', height:'30px'}} /> </div>
						<div  className="font-bold text-lg">Loading...</div>
					</div>
				</>
			);
		}
	}
	function EmptyRecordMessage(){
		if(pageReady && !records.length){
			return (
				<div className="text-lg mt-3 p-3 text-center text-400 font-bold">
					Tidak Ada Di Temukan !
				</div>
			);
		}
	}
	function MultiDelete() {
		if (selectedItems.length) {
			return (
				<div className="m-2 flex-grow-0">
					<Button onClick={() => deleteItem(selectedItems)} icon="pi pi-trash" className="p-button-danger" title="Delete Selected"/>
				</div>
			)
		}
	}
	function PagerControl() {
		if (props.paginate && totalPages > 1) {
		const pagerReportTemplate = {
			layout: pagination.layout,
			CurrentPageReport: (options) => {
				return (
					<>
						<span className="text-sm text-gray-500 px-2">Page <b>{ options.currentPage } of { options.totalPages }</b></span>
						<span className="text-sm text-gray-500 px-2">Records <b>{ recordsPosition } of { options.totalRecords }</b></span>
					</>
				);
			}
		}
		return (
			<div className="flex-grow-1">
				<Paginator first={firstRow} rows={limit} totalRecords={totalRecords}  rowsPerPageOptions={[5, 10, 20, 30, 50, 100, 200, 500, 1000]}  onPageChange={onPageChange} template={pagerReportTemplate} />
			</div>
		)
		}
	}
	function PageFooter() {
		if (pageReady && props.showFooter) {
			return (
				<div className="flex flex-wrap justify-content-between">
					<MultiDelete />
					<PagerControl />
				</div>
			);
		}
	}
	function PageBreadcrumbs(){
		if(props.showBreadcrumbs) {
			const items = getPageBreadCrumbs();
			return (items.length > 0 && <BreadCrumb className="mb-3" model={items} />);
		}
	}
	if(apiRequestError){
		return (
			<PageRequestError error={apiRequestError} />
		);
	}
	return (
<main id="KbmGuruListPage" className="main-page">
    { (props.showHeader) && 
    <section className="page-section mb-3" >
        <div className="container-fluid">
            <div className="grid justify-content-between align-items-center">
                <div className="col " >
                    <Title title="Data Kbm Guru"   titleClass="text-2xl text-primary font-bold" subTitleClass="text-sm text-500"      separator={false} />
                </div>
                <div className="col-12 md:col-3 " >
                    <Link to={`/kbm_guru/add`}>
                        <Button label="Tambah kbm" icon="pi pi-plus" type="button" className="p-button bg-primary "  />
                        </Link>
                    </div>
                    <div className="col-12 md:col-3 " >
                        <div className="card p-3 mb-4">
                            <DataSource onLoad={(options) => setFilterOptions('kbm_guru_nama', options)}  apiPath="components_data/nama_option_list" >
                                {
                                ({ response  }) => 
                                <>
                                <Title title="Pencarian "   titleClass="font-bold text-lg" subTitleClass="text-sm text-500"      separator={false} />
                                <div className="px-3 pb-3">
                                    <Dropdown filter={true} className="w-full" onChange={(e) => setFilterValue( 'kbm_guru_nama', e.value)} value={filters.kbm_guru_nama.value} optionLabel="label" optionValue="value" options={filters.kbm_guru_nama.options} placeholder="Cari Nama Guru" >
                                    </Dropdown>
                                </div>
                                </>
                                }
                            </DataSource>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        }
        <section className="page-section " >
            <div className="container-fluid">
                <div className="grid ">
                    <div className="col comp-grid" >
                        <FilterTags filterController={pageFilterController} />
                        <div >
                            <PageBreadcrumbs />
                            <div className={`${!props.isSubPage ? 'card ' : ''}`}>
                                <div className="page-records">
                                    <DataTable 
                                        lazy={true} 
                                        loading={loading} 
                                        selectionMode="checkbox" selection={selectedItems} onSelectionChange={e => setSelectedItems(e.value)}
                                        value={records} 
                                        dataKey="id_kehadiran_ajar" 
                                        sortField={sortBy} 
                                        sortOrder={sortOrder} 
                                        onSort={onSort}
                                        className=" p-datatable-sm" 
                                        stripedRows={true}
                                        showGridlines={false} 
                                        rowHover={true} 
                                        responsiveLayout="stack" 
                                        emptyMessage={<EmptyRecordMessage />} 
                                        >
                                        {/*PageComponentStart*/}
                                        <Column selectionMode="multiple" headerStyle={{width: '2rem'}}></Column>
                                        <Column  field="tanggal" header="Tanggal"   ></Column>
                                        <Column  field="nama" header="Nama Guru" body={NamaTemplate}  ></Column>
                                        <Column  field="bidang_study" header="Mata Pelajaran" body={BidangStudyTemplate}  ></Column>
                                        <Column  field="kelas" header="Kelas" body={KelasTemplate}  ></Column>
                                        <Column  field="jurusan" header="Jurusan"   ></Column>
                                        <Column  field="jam_ke" header="Jam Mengajar " body={JamKeTemplate}  ></Column>
                                        <Column  field="kehadiran" header="Kehadiran"   ></Column>
                                        <Column  field="keterangan" header="Keterangan"   ></Column>
                                        <Column headerStyle={{width: '2rem'}} headerClass="text-center" body={ActionButton}></Column>
                                        {/*PageComponentEnd*/}
                                    </DataTable>
                                </div>
                                <PageFooter />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </main>
	);
}
KbmGuruListPage.defaultProps = {
	primaryKey: 'id_kehadiran_ajar',
	pageName: 'kbm_guru',
	apiPath: 'kbm_guru/index',
	routeName: 'kbm_gurulist',
	msgBeforeDelete: "Yakin Menghapus Data Ini?",
	msgTitle: 'Delete Record',
	msgAfterDelete: "Data Berhasil Di Hapus !",
	showHeader: true,
	showFooter: true,
	paginate: true,
	isSubPage: false,
	showBreadcrumbs: true,
	exportData: false,
	importData: false,
	keepRecords: false,
	multiCheckbox: true,
	search: '',
	fieldName: null,
	fieldValue: null,
	sortField: '',
	sortDir: '',
	pageNo: 1,
	limit: 20,
}
export default KbmGuruListPage;
